          <a href="https://t.me/whowashere" class="link tg" target="_blank">
            <i class="fab fa-telegram"></i>
          </a>
          <a href="https://vk.com/kirkland197" class="link vk" target="_blank">
            <i class="fab fa-vk"></i>
          </a>
          <a href="mailto:iulugbek21@gmail.com" class="link mail" target="_blank">
            <i class="fas fa-at"></i>
          </a>
          <!-- mailto:iulugbek21@gmail.com?subject=%D0%9F%D1%80%D0%B8%D0%B2%D0%B5%D1%82%2C%20%D0%B2%D0%BE%D1%82%20%D0%BC%D0%BE%D0%B5%20%D0%A2%D0%97&body=%D0%A1%D1%8E%D0%B4%D0%B0%20%D1%84%D0%B0%D0%B9%D0%BB -->
